import Vue from 'vue'
import Vuex from 'vuex'
import menus from './utils/menus'//menus
import initData from './database/initData'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menus,
    defaultMenu: menus[menus.length - 1],
    githubUrl: "https://github.com/cycxy",
    navItem: menus[0],
    ...initData,
  },
  mutations: {
    setStore(state, payload) {
      for (const plkey in payload) {
        state[plkey] = payload[plkey]
      }
      console.log(state)
    }
  },
  actions: {
    setStore(context, payload) {
      return new Promise((resolve) => {
        context.commit('setStore', payload)
        resolve()
      })

    }
  },
  getters: {
    getMenus: function (state) {
      return state.menus
    },
    getNavItm: function (state) {
      return state.navItem
    },
    getUser: function (state) {
      return state.loginUser
    },
    getSuperior: function (state) {
      return state.superior
    }
  }
})
